import { useRecoilState } from 'recoil';
import { cityState }from '../recoil/atom'

const WeatherButton = () => {
    let [city,setCity] = useRecoilState(cityState);
  return (
    <ul className='buttonList'>
        <li className='buttonList--item' onClick={()=>{setCity('India')}}></li>
        <li className='buttonList--item' onClick={()=>{setCity('China')}}></li>
        <li className='buttonList--item' onClick={()=>{setCity('france')}}></li>
        <li className='buttonList--item' onClick={()=>{setCity('Pakistan')}}></li>
        <li className='buttonList--item' onClick={()=>{setCity('Kazakhstan')}}></li>
        <li className='buttonList--item' onClick={()=>{setCity('Uzbekistan')}}></li>
        <li className='buttonList--item' onClick={()=>{setCity('Mexico')}}></li>
    </ul>
  )
}

export default WeatherButton